import React, { useState } from 'react';
import { Link } from 'gatsby';
import './about.css';
import Modal from './modal';
import Video from '../assets/video.mp4';
import { galleryData } from '../data/galleryData';

const Gallery = () => {

  const [modal, setModal] = useState();
  const [numGroups, setNumGroups] = useState(1);

  const numImagesInGroup = 12;

  const showModal = (imageIndex, groupIndex) => {
    const lastImageIndex = (numGroups * numImagesInGroup) - 1;

    const absoluteIndex = groupIndex === 0
      ? imageIndex
      : (groupIndex * numImagesInGroup) + imageIndex;

    setModal(<Modal 
      startIndex = { absoluteIndex }
      imageItems = { galleryData }
      onClose = { () => setModal(null) }
      maxNumber = { lastImageIndex }
      />);
  };

  return (
    <>
      {modal}
      <div className='galleryContainer'>
        <div className='innerContainer'>
          <h1>Our Projects</h1>
            <div>
              {Array.from({length: numGroups}, (_, groupIndex) => 
              {
                const start = groupIndex * numImagesInGroup;
                const end = start + numImagesInGroup;

                return <div className='gallery' key = {groupIndex}>
                  {galleryData.slice(start, end).map((e, i) => 
                    <div 
                      className='imgBox'
                      key={i}
                      onClick={() => showModal(i, groupIndex)}
                    >
                      <img 
                      src={e.image} 
                      alt='Project images'
                      />
                    </div>
                  )}
              </div>})}
            </div>
            <button
              className='showMoreImage fontTwo'
              onClick={() =>
                (galleryData.length < (numGroups + 1)*12) ? null : setNumGroups(numGroups + 1)
              }>
                Show More  <i className='fas fa-caret-down fontTwo'></i>
            </button>
        </div>
      </div>
    </>
  );
};

const About = () => {
  return (
    <div id='about'>
      <Gallery />
      <div className='aboutContainer'>
        <div className='flexRight flexHeight'>
            <video autoPlay loop preload='auto'>
              <source src={Video} type='video/mp4'></source>
              <track default/>
            </video>
        </div>
        <div className='flexLeft flexHeight'>
          <div className='fontFour textContainer'>
            <h2>Our Services</h2>
            <p>Our mission is your wish</p>
            <Link to='/service'>
              <i className='fas fa-angle-double-left'>
              </i>  Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;